.parent {
  padding-left: 50px;
  padding-right: 50px;
}

.edit__card {
  display: flex;
  flex-direction: row-reverse;
  margin-top: 10px;
  gap: 10px;
}

.part__card {
  display: flex;
  flex-direction: row;
}

.card__part1 {
  width: 70%;
}

.card__title {
  display: flex;
  flex-direction: row;
  margin-top: 10px;
  gap: 10px;
}

.btn__edit {
  border: none;
  cursor: pointer;
}

.card__img {
  width: 30%;
}

.tags__btn {
  display: inline-block;
  box-sizing: border-box;
  padding: 0 25px;
  margin: 0 15px 15px 0;
  outline: none;
  border: 1px solid #fff;
  border-radius: 50px;
  height: 46px;
  line-height: 46px;
  font-size: 14px;
  font-weight: 600;
  text-decoration: none;
  color: #ffffff;
  background-color: rgb(86, 25, 25);
  box-shadow: 0 4px 6px rgb(65 132 144 / 10%), 0 1px 3px rgb(0 0 0 / 8%);
  cursor: pointer;
  user-select: none;
  appearance: none;
  touch-action: manipulation;
  vertical-align: top;
  transition: box-shadow 0.2s;
}

.no__user {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
